@import url('https://fonts.googleapis.com/css2?family=Merriweather&family=Open+Sans&family=Source+Code+Pro&display=swap');

* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

body {
	font-family: 'Open Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell',
		'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	min-height: 100vh;
	font-size: 1rem;
	overflow-x: hidden;
}

a:link,
a:visited {
	text-decoration: none;
	color: var(--quaternary);
}

a:hover {
	color: var(--tertiary);
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: 'Merriweather', serif;
}

h1 {
	font-size: 3.052rem;
}

h2 {
	font-size: 2.441rem;
}

h3 {
	font-size: 1.953rem;
}

h4 {
	font-size: 1.563rem;
}

h5 {
	font-size: 1.25rem;
}

#root {
	height: 100vh;
}

code {
	font-family: 'Source Code Pro', Menlo, Monaco, Consolas, 'Courier New', monospace;
}

@media screen and (max-width: 768px) {
	html {
		font-size: 80%;
	}
}
